import { defineStore } from 'pinia'

export const useStore = defineStore('chart', {
    state : () => ({
        chartData : [],
    }),
    actions : {
        setChartData(data) {
            this.chartData = data;
        }
    },
    persist: {
        enabled : true,
        strategies : [
            {
                key : 'chartStore',
                storage : localStorage,
            }
        ]
    }
})